/* eslint-disable no-unused-vars */
import React from "react"
import {  Button,message } from "antd"
import { caculate,TR } from "./show"
import './index.css'

class ESDemo extends React.Component {
  constructor(){
    super()
    this.state={
    }
    this.myRef = React.createRef();

  }


  componentDidMount(){
    
    console.log(caculate(1,23,3,4,5))
    console.log(TR.info())
    
  }

  componentWillUnmount(){
    console.log('antd')
  }

  componentDidUpdate(){
    console.log('update')
  }

  show=()=>{
   this.loadImageAsync('https://avatars.githubusercontent.com/u/51108500?v=4').then((img)=>{
     this.myRef.current.append(img)
     message.success('load avatars success')
   },(err)=>{
     message.info(err)
   })
    
  }

  loadImageAsync=(url)=>{
    return new Promise((resolve,reject)=>{
      let img=new Image()

      img.onload=function(){
        resolve(img)
      }

      img.onerror=function(){
        reject('load error')
      }
      img.src=url
    })
  }

  render() {

    return (
      <div ref={this.myRef}>
        <Button onClick={this.show} ></Button>
      </div>
    )
  }
}

export default ESDemo
